// src/apis/friendshipAPI.ts
const SERVER_URL = 'http://localhost:3001';

export interface Friend {
    friend_username: string;
    created_at: string;
    status: string;
}

export async function fetchFriends(username: string | undefined): Promise<Friend[]> {
    if (username === undefined) throw new Error('Username not provided');
    const response = await fetch(`${SERVER_URL}/friends/${username}`, {
        method: 'GET',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
    });
    if (!response.ok) throw new Error('Failed to fetch friends data');
    return response.json();
}

export async function sendFriendRequest(username: string | undefined, friendUsername: string): Promise<void> {
    if (!username) throw new Error('Username not provided');
    if (!friendUsername) throw new Error('Friend username not provided');
    const response = await fetch(`${SERVER_URL}/friends/request`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, friendUsername }),
        credentials: 'include',
    });
    if (!response.ok) throw new Error('Failed to send friend request');
}

