// src/api.ts
const SERVER_URL = 'http://localhost:3001';

type Workout = {
    id: number;
    name: string;
    difficulty: 'easy' | 'intermediate' | 'advanced';
    description: string;
};

// Fetch all workouts
export async function fetchAllWorkouts(): Promise<Workout[]> {
    const response = await fetch(SERVER_URL+'/workouts/all');
    if (!response.ok) throw new Error('Failed to fetch recommended workouts');
    return response.json();
}

// Fetch suggested workout
export async function fetchSuggestedWorkout(): Promise<Workout | null> {
    const response = await fetch(SERVER_URL+'/workouts/suggested');
    if (!response.ok) throw new Error('Failed to fetch suggested workout');
    const data = await response.json();
    return data.length > 0 ? data[0] : null; // Assume the first item is suggested
}

// Fetch workouts by difficulty
export async function fetchWorkoutsByDifficulty(difficulty: 'easy' | 'intermediate' | 'advanced'): Promise<Workout[]> {
    const response = await fetch(SERVER_URL+`/workouts/difficulty/${difficulty}`);
    if (!response.ok) throw new Error(`Failed to fetch ${difficulty} workouts`);
    return response.json();
}
