import { Box, Typography, List, ListItem, ListItemText, Avatar, TextField, Button } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { fetchFriends, Friend, sendFriendRequest } from './apis/friendshipAPI';

interface UserProps {
    username: string | undefined;
    role: string | undefined;
    handleLogout: () => void;
}

const Friendship: React.FC<UserProps> = ({ username }) => {
    const [friends, setFriends] = useState<Friend[]>([]);
    const [newFriendUsername, setNewFriendUsername] = useState<string>(''); // State for form input
    const [requestStatus, setRequestStatus] = useState<string | null>(null); // Feedback for request status

    const imageUrl = 'http://localhost:3001/static/boy.png';

    useEffect(() => {
        async function fetchData() {
            try {
                const friendsData = await fetchFriends(username);
                setFriends(friendsData);
            } catch (error) {
                console.error('Error fetching friends data:', error);
            }
        }
        fetchData();
    }, [username]);

    const handleSendRequest = async () => {
        if (!newFriendUsername) return;
        
        try {
            await sendFriendRequest(username, newFriendUsername);
            setRequestStatus(`Friend request sent to ${newFriendUsername}!`);
            setNewFriendUsername(''); // Reset input field
        } catch (error) {
            setRequestStatus('Failed to send friend request. Please try again.');
            console.error('Error sending friend request:', error);
        }
    };

    return (
        <>
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    gap: 2,
                    mt: 8,
                    width: '100%',
                    maxWidth: 360,
                    mx: 'auto'
                }}
            >
                <Typography variant="h4" component="h1" gutterBottom color="white">
                    Hi {username}!
                </Typography>
                <Typography variant="h5" component="h1" gutterBottom color="white">
                    Your Friends
                </Typography>

                {/* Display list of friends */}
                <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
                    {friends.length > 0 ? (
                        friends.filter((friend) => friend.status === 'accepted').map((friend, index) => (
                            <ListItem key={index} sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                                
                                <Avatar
                                    alt={username}
                                    src={imageUrl}
                                    sx={{ width: 56, height: 56, marginRight: 6, marginLeft: 4 }}
                                />
                                <ListItemText
                                    primary={friend.friend_username}
                                    secondary={
                                        <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                                            <span style={{ color: 'gray' }}>{`Status: ${friend.status}`}</span>
                                            <span style={{ color: 'gray' }}>{`Friend since: ${new Date(friend.created_at).toLocaleDateString()}`}</span>
                                        </Box>
                                    }
                                />
                            </ListItem>
                        ))
                    ) : (
                        <Typography color="textSecondary">
                            You have no friends added yet.
                        </Typography>
                    )}
                </List>

                <Typography variant="h5" component="h1" gutterBottom color="white">
                    Pending Request
                </Typography>

                <List sx={{ width: '100%' }}>
                    {friends.filter((friend) => friend.status === 'pending').length > 0 ? (
                        friends.filter((friend) => friend.status === 'pending').map((friend, index) => (
                            <ListItem key={index} sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                                
                                <Avatar
                                    alt={username}
                                    src={imageUrl}
                                    sx={{ width: 56, height: 56, marginRight: 6, marginLeft: 4 }}
                                />
                                <ListItemText
                                    primary={friend.friend_username}
                                    secondary={
                                        <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                                            <span style={{ color: 'gray' }}>{`Status: ${friend.status}`}</span>
                                            <span style={{ color: 'gray' }}>{`Friend since: ${new Date(friend.created_at).toLocaleDateString()}`}</span>
                                        </Box>
                                    }
                                />
                            </ListItem>
                        ))
                    ) : (
                        <Typography variant="body1" component="h1" gutterBottom color="white" style={{ textAlign: 'center' }}>
                            You don't have any pending request.
                        </Typography>
                    )}
                </List>

                {/* Friend request form */}
                <Typography variant="h5" component="h1" gutterBottom color="white">
                    Add a New Friend
                </Typography>
                <TextField
                    label="Friend's Username"
                    variant="outlined"
                    value={newFriendUsername}
                    onChange={(e) => setNewFriendUsername(e.target.value)}
                    fullWidth
                    sx={{ bgcolor: 'white' }}
                />
                <Button variant="contained" color="primary" onClick={handleSendRequest} fullWidth sx={{ mt: 2, mb:4 }}>
                    Send Friend Request
                </Button>
                {requestStatus && (
                    <Typography variant="body2" color="textSecondary" sx={{ mt: 1, textAlign: 'center' }}>
                        {requestStatus}
                    </Typography>
                )}
            </Box>
        </>
    );
};

export { Friendship };
