import { Box, Typography, Card, CardContent } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { fetchAllWorkouts, fetchSuggestedWorkout, fetchWorkoutsByDifficulty } from './apis/workoutAPI';

type Workout = {
  id: number;
  name: string;
  difficulty: 'easy' | 'intermediate' | 'advanced';
  description: string;
};

const Workout: React.FC = () => {
  const [recommendedWorkouts, setRecommendedWorkouts] = useState<Workout[]>([]);
  const [suggestedWorkout, setSuggestedWorkout] = useState<Workout | null>(null);
  const [workoutsByDifficulty, setWorkoutsByDifficulty] = useState({
    easy: [] as Workout[],
    intermediate: [] as Workout[],
    advanced: [] as Workout[],
  });

  useEffect(() => {
    async function fetchData() {
      try {
        const allWorkouts = await fetchAllWorkouts();

        setRecommendedWorkouts(allWorkouts);

        const suggested = await fetchSuggestedWorkout();
        setSuggestedWorkout(suggested);

        // Fetch workouts by difficulty
        const easyWorkouts = await fetchWorkoutsByDifficulty('easy');
        const intermediateWorkouts = await fetchWorkoutsByDifficulty('intermediate');
        const advancedWorkouts = await fetchWorkoutsByDifficulty('advanced');

        setWorkoutsByDifficulty({
          easy: easyWorkouts,
          intermediate: intermediateWorkouts,
          advanced: advancedWorkouts,
        });
      } catch (error) {
        console.error('Error fetching workout data:', error);
      }
    }

    fetchData();
  }, []);

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: 4,
        mt: 8,
        width: '100%',
        maxWidth: 360,
        mx: 'auto',
        px: 2,
      }}
    >

      {/* Suggested Workout Section */}
      {suggestedWorkout && (
        <Box sx={{ width: '100%' }}>
          <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, color: 'white' }}>
            Suggested Workout for You
          </Typography>
          <Card sx={{ backgroundColor: '#d1e7dd', mb: 2 }}>
            <CardContent>
              <Typography variant="body1" sx={{ fontWeight: 'medium' }}>
                {suggestedWorkout.name}
              </Typography>
            </CardContent>
          </Card>
        </Box>
      )}

      {/* Workouts by Difficulty Section */}
      <Box sx={{ width: '100%' }}>
        {/* Easy Workouts */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h5" sx={{ fontWeight: 'medium', color: 'white' }}>
            Easy
          </Typography>
          {workoutsByDifficulty.easy.map((workout) => (
            <Card key={workout.id} sx={{ mb: 1, backgroundColor: '#e3f2fd' }}>
              <CardContent>
                <Typography>{workout.name}</Typography>
              </CardContent>
            </Card>
          ))}
        </Box>

        {/* Intermediate Workouts */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h5" sx={{ fontWeight: 'medium', color: 'white' }}>
            Intermediate
          </Typography>
          {workoutsByDifficulty.intermediate.map((workout) => (
            <Card key={workout.id} sx={{ mb: 1, backgroundColor: '#ffecb3' }}>
              <CardContent>
                <Typography>{workout.name}</Typography>
              </CardContent>
            </Card>
          ))}
        </Box>

        {/* Advanced Workouts */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h5" sx={{ fontWeight: 'medium', color: 'white' }}>
            Advanced
          </Typography>
          {workoutsByDifficulty.advanced.map((workout) => (
            <Card key={workout.id} sx={{ mb: 1, backgroundColor: '#ffccbc' }}>
              <CardContent>
                <Typography>{workout.name}</Typography>
              </CardContent>
            </Card>
          ))}
        </Box>

        {/* Recommended Workouts Section */}
        <Box sx={{ width: '100%' }}>
          <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, color: 'white' }}>
            All Workouts
          </Typography>
          {recommendedWorkouts.map((workout) => (
            <Card key={workout.id} sx={{ mb: 1, backgroundColor: '#f5f5f5' }}>
              <CardContent>
                <Typography>{workout.name}</Typography>
              </CardContent>
            </Card>
          ))}
        </Box>
      </Box>
    </Box>
  );
};

export { Workout };
