// src/components/Register.tsx
import React, { useState } from 'react';
import { TextField, Button, Typography, Box, Link } from '@mui/material';
import { registerUser } from './apis/userAPI';

interface RegisterProps {
    handleRegister: (credentials: { username: string; password: string; role?: string }) => void;
}

const Register: React.FC<RegisterProps> = ({ handleRegister }) => {
    const [username, setUsername] = useState<string>('');
    const [password, setPassword] = useState<string>('');
    const [confirmPassword, setConfirmPassword] = useState<string>('');
    const [error, setError] = useState<string | null>(null);

    const onRegisterClick = async () => {
        if (password !== confirmPassword) {
            setError('Passwords do not match');
            return;
        }
        setError(null);

        try {
            await registerUser(username, password, "user"); // You can replace "user" with any role logic you need
            handleRegister({ username, password }); // Optional: if handleRegister is for post-registration actions
        } catch (err) {
            setError('Registration failed. Please try again.');
        }
    };

    return (
        <Box
            sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                gap: 2,
                mt: 8,
                width: '100%',
                maxWidth: 360,
                mx: 'auto',
            }}
        >
            <Typography variant="h4" component="h1" gutterBottom color="white">
                Register
            </Typography>

            <TextField
                label="Username"
                variant="outlined"
                fullWidth
                required
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                InputProps={{
                    style: { borderColor: 'white' },
                }}
                InputLabelProps={{
                    style: { color: 'white' },
                }}
                sx={{
                    '& .MuiOutlinedInput-root': {
                        '& fieldset': { borderColor: 'white' },
                        '&:hover fieldset': { borderColor: 'white' },
                        '&.Mui-focused fieldset': { borderColor: 'white' },
                    },
                    '& .MuiInputBase-input': { color: 'white' },
                }}
            />

            <TextField
                label="Password"
                type="password"
                variant="outlined"
                fullWidth
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                InputProps={{
                    style: { borderColor: 'white' },
                }}
                InputLabelProps={{
                    style: { color: 'white' },
                }}
                sx={{
                    '& .MuiOutlinedInput-root': {
                        '& fieldset': { borderColor: 'white' },
                        '&:hover fieldset': { borderColor: 'white' },
                        '&.Mui-focused fieldset': { borderColor: 'white' },
                    },
                    '& .MuiInputBase-input': { color: 'white' },
                }}
            />

            <TextField
                label="Confirm Password"
                type="password"
                variant="outlined"
                fullWidth
                required
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                InputProps={{
                    style: { borderColor: 'white' },
                }}
                InputLabelProps={{
                    style: { color: 'white' },
                }}
                sx={{
                    '& .MuiOutlinedInput-root': {
                        '& fieldset': { borderColor: 'white' },
                        '&:hover fieldset': { borderColor: 'white' },
                        '&.Mui-focused fieldset': { borderColor: 'white' },
                    },
                    '& .MuiInputBase-input': { color: 'white' },
                }}
            />

            {error && (
                <Typography color="error" variant="body2" sx={{ mt: 1 }}>
                    {error}
                </Typography>
            )}

            <Button
                fullWidth
                variant="contained"
                color="primary"
                sx={{ mt: 2, backgroundColor: 'black', color: 'white', borderRadius: 20 }}
                onClick={onRegisterClick}
            >
                Register
            </Button>

            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    width: '100%',
                    mt: 5,
                    textAlign: 'center',
                }}
            >
                <Typography variant="body2" sx={{ color: '#fff', fontSize: '1.1rem' }}>
                    Already have an account?
                </Typography>

                <Link
                    href="/login"
                    variant="body2"
                    sx={{
                        fontWeight: 'bold',
                        color: '#fff',
                        textDecoration: 'none',
                        fontSize: '1.2rem',
                        mt: 3,
                    }}
                >
                    Login
                </Link>
            </Box>
        </Box>
    );
};

export { Register };
